import {connect} from "react-redux";
import {fetchCart, fetchMiniCart} from "eshop/modules/cart/actions/cart";
import MulticartSummaryComponent from "eshop/modules/cart/components/MulticartSummaryComponent";
import {getEntryType,getHasCartData} from "../selectors";
import { getModifyConsentsInCartQueue } from "eshop/modules/checkout/selectors";
import {updatingAnyConsent} from 'eshop/modules/checkout/selectors';

const mapDispatchToProps = (dispatch) => ({
    fetchCart: () => dispatch(fetchCart()),
    fetchMiniCart: () => dispatch(fetchMiniCart())
});

const mapStateToProps = state => ({
    updating: getModifyConsentsInCartQueue(state).length > 0 || updatingAnyConsent(state),
    entryType: getEntryType(state),
    hasCartData:getHasCartData(state)
});

export default connect(mapStateToProps, mapDispatchToProps)(MulticartSummaryComponent);